.bgColor {
  background-color: transparent;
}
.bgColor :hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.height {
  height: 40px;
}

.textAlign {
  height: 40px;
  line-height: 40px;
  text-align: center;
}

@media (max-width: 764px) {
  .border {
    border: none;
  }
}

@media (min-width: 764px) {
  .paddingComponent {
    padding: 0;
  }

  .textAlign {
    height: 40px;
    line-height: 40px;
    text-align: center;
  }
}
